import React from 'react';
import {
     Routes, Route, useNavigate
} from "react-router-dom";
import Builder from '../pages/builder';
import PageList from '../pages/pageList';
import Renderer from '../pages/renderer';

function HomeRouter() {
     const navigate = useNavigate();
     const handleIdeRoute = (pageId:any) => {
          navigate("craftsman2/main/pages/ide", {state:pageId});
     }

     const handleNewRoute = () => {
          navigate("craftsman2/main/pages/ide");
     }

     const handleBackHome = () => {
          navigate("craftsman2/main/pages/");
     }

     return (
     <Routes>
        <Route path="craftsman2/main/pages/ide" element={<Builder backHomeRoute={handleBackHome}/>} />
        <Route path="craftsman2/main/pages/render/:pageId" element={<Renderer />} />
        <Route index path="craftsman2/main/pages" element={<PageList ideRoute={handleIdeRoute} newIdeRoute={handleNewRoute}/>} />
     </Routes>
     )
}

export default HomeRouter;